.header{
  display: flex;
  align-items: center;
  height: 85px;
  width: 1917px;
  border-bottom: 1.7px solid rgba(255, 255, 255, 0.062);
  background: linear-gradient(91deg, rgba(217, 217, 217, 0.18) -5.03%, rgba(217, 217, 217, 0.00) -5.02%, rgba(217, 217, 217, 0.04) 27.86%, rgba(217, 217, 217, 0.03) 46.25%, rgba(217, 217, 217, 0.06) 89.31%, rgba(217, 217, 217, 0.00) 102%);
  backdrop-filter: blur(5px);
}

.contrate {
  background: linear-gradient(180deg, rgb(0, 86.7, 255) 0%, rgb(188.7, 0, 255) 100%);
  background-color: rgba(255, 255, 255, 1);
  border-radius: 10.54px;
  height: 42px;
  overflow: hidden;
  position: relative;
  width: 142px;
}

.header .botoes{
  background-color: aliceblue;
  display: flex;
  align-items: center;
}

.behance{
  background-color: antiquewhite;
  width: 10px;
  height: 10px;
}